import { makeStyles } from '@material-ui/core';
import { ChevronLeft, ChevronRight } from '@material-ui/icons';
import classNames from 'classnames';
import React, { useCallback, useEffect, useState } from 'react';
import { mod } from '../utils/base';

const TRANSITION_TIME_MS = 1000;
const SLIDE_DURATION_MS = 3500;

const useStyles = makeStyles((theme) => ({
  contentContainer: {
    position: 'relative',
  },
  image: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100vw',
    transition: `all ${TRANSITION_TIME_MS / 2000}s ease-in`,
    opacity: 0,
  },
  leftButton: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '50vw',
    height: '100%',
    cursor: 'pointer',
    display: 'flex',
    justifyContent: 'flex-start',
    '&:hover > $chevron': {
      opacity: 1,
    }
  },
  rightButton: {
    position: 'absolute',
    top: 0,
    right: 0,
    width: '50vw',
    height: '100%',
    cursor: 'pointer',
    display: 'flex',
    justifyContent: 'flex-end',
    '&:hover > $chevron': {
      opacity: 1,
    }
  },
  chevron: {
    opacity: 0,
    background: 'rgba(0 0 0 / 50%)',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    transition: 'opacity .2s ease-in',
    '& > svg': {
      fill: 'white',
      height: '4rem',
      width: '4rem',
    },
  },
  blur: {
    filter: 'blur(36px)',
  },
  activeImage: {
    position: 'relative',
    opacity: 1,
  }
}));

type BlurStepperProps = { steps: { image: string, label?: string }[] };
const BlurStepper = ({ steps }: BlurStepperProps) => {
  const classes = useStyles();
  const [activeIdx, setActiveIdx] = useState(0);
  const [showBlur, setShowBlur] = useState(false);
  const [autoPlayId, setAutoPlayId] = useState<NodeJS.Timeout | null>(null);

  const handleClick = (incr: number) => () => {
    setActiveIdx((prevIdx) => mod(prevIdx + incr, steps.length));
    setShowBlur(true);
    autoPlayId && clearTimeout(autoPlayId);
    setTimeout(() => setShowBlur(false), TRANSITION_TIME_MS / 2);
  }

  const incrementImage = useCallback(handleClick(1), []);
  useEffect(() => {
    const id = setTimeout(() => {
      !showBlur && incrementImage();
    }, SLIDE_DURATION_MS);
    setAutoPlayId(id);
    return () => clearTimeout(id);
  }, [showBlur, incrementImage]);

  return (
    <div className={classes.contentContainer}>
      {Array(3).fill(1).map((v, idx) => (
        <img key={steps[mod(activeIdx - 1 + idx, steps.length)].image} src={steps[mod(activeIdx - 1 + idx, steps.length)].image} className={classNames(classes.image, { [classes.blur]: showBlur, [classes.activeImage]: idx === 1 })} alt={steps[activeIdx].label} />
      ))}
      <div onClick={handleClick(-1)} className={classes.leftButton}>
        <div className={classes.chevron}><ChevronLeft /></div>
      </div>
      <div onClick={handleClick(1)} className={classes.rightButton}>
        <div className={classes.chevron}><ChevronRight /></div>
      </div>
    </div>
  );
};

export default BlurStepper;